import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DashboardService } from 'src/app/dashboard/services/dashboard.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-edit-employee-info',
  templateUrl: './edit-employee-info.component.html',
  styleUrls: ['./edit-employee-info.component.css']
})
export class EditEmployeeInfoComponent implements OnInit {
  employeeForm: FormGroup;
  employeeArr = [];
  index: any;
  id: any;
  submitted = false;

  empObj: { name: any; designation: any; salary: any; index: any; };

  constructor(private formBuilder: FormBuilder, private dashboardservice : DashboardService,
    private router : Router,private route: ActivatedRoute,private toastr: ToastrService) { }

  ngOnInit() {
    this.id = this.route.params['_value'].id;
    this.getEmployeeData();
    this.initializeForm();
    // this.getEmployeeData();
  }


  initializeForm(){
    this.employeeForm = this.formBuilder.group({
      name: [ (this.id === "1" ? this.employeeArr['name'] : '' ) ,Validators.required ],
      designation: [ (this.id === "1" ? this.employeeArr['designation'] : ''),Validators.required],
      salary: [(this.id === "1" ? this.employeeArr['salary'] : ''),Validators.required]
     
  });
  }

  getEmployeeData(){
    this.dashboardservice.getEmployeeInfo.subscribe(data => {
      if(data){
        this.employeeArr = data.dataEmp
        this.index = data.indexVal
      
      }
    })
  }

  cancel(){
    this.router.navigate(['/'])

  }
  get f() { return this.employeeForm.controls; }

  submit(){
    this.submitted = true;

    // stop here if form is invalid
    if (this.employeeForm.invalid) {
        return;
    }

    if(this.id === "1"){
      this.empObj = {
        name : this.employeeForm.get('name').value,
        designation : this.employeeForm.get('designation').value,
        salary : this.employeeForm.get('salary').value,
        index : this.index
      }
      this.toastr.success('Employee Record Updated Successfully');

    }
    else{
      this.empObj = {
        name : this.employeeForm.get('name').value,
        designation : this.employeeForm.get('designation').value,
        salary : this.employeeForm.get('salary').value,
        index : null
      }
      this.toastr.success('Employee Record Added Successfully');

    }


    this.dashboardservice.storeEmployeeObjInfo(this.empObj);
    this.router.navigate(['/'])

  }

}
