import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DashboardService } from '../../services/dashboard.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { DeleteDialogComponent } from './delete-dialog/delete-dialog.component';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  search : any;
  employeeArr = [
    {
      name : 'Haushiram',
      designation : 'Software Developer',
      salary : '25000'
    },
    {
      name : 'Ketan',
      designation : 'Software Analyst',
      salary : '30000'
    },
    {
      name : 'Amol',
      designation : 'Tester',
      salary : '29000'
    },
    {
      name : 'Rahul',
      designation : 'Software Developer',
      salary : '28000'
    },
    {
      name : 'Anup',
      designation : 'UI/UX',
      salary : '100000'
    },
    {
      name : 'Swap',
      designation : 'Tester',
      salary : '19000'
    },
    {
      name : 'Akash',
      designation : 'Admin',
      salary : '30000'
    }
  ]

  constructor(private router : Router, private dashboardservice : DashboardService,
    public dialog: MatDialog,private toastr: ToastrService) { }

  ngOnInit() {
    this.dashboardservice.getEmployeeObjInfo.subscribe(data => {
      if(data){
        if(data.index != null){
          let index = data.index;
          this.employeeArr[index] = data
        }
        else{
          this.employeeArr.push(data)
        }
       
      }
    })
  }

  editInfo(employeeDetails,index){
    let obj = {
      dataEmp  : employeeDetails,
      indexVal : index
    }
    this.dashboardservice.storeEmployeeInfo(obj) // storing data in behaviour subject here
    this.router.navigate(['edit-employee-info',1]) 
  }

  delete(index){
      const dialogRef = this.dialog.open(DeleteDialogComponent, {
        width: '400px',
        height : '200px',
        data: {

        }
      });
  
      dialogRef.afterClosed().subscribe(result => {
       if(result){
        this.employeeArr.splice(index,1)
        this.toastr.success('Employee Record Deleted Successfully');
       }
      });

  }

  addnew(){
    this.router.navigate(['edit-employee-info',0])

  }

}
